.price-list {
  display: flex;
  justify-content: space-around;
  &--reverse {
    flex-direction: row-reverse;
  }
  @media screen and(max-width: $laptopWidth) {
    flex-direction: column;
    align-items: center;
    &--reverse {
      flex-direction: column;
    }
  }
  &__table {
    width: 40%;
    @media screen and(max-width: $laptopWidth) {
      width: 80%;
      margin-bottom: 3rem;
    }
    @media screen and(max-width: $mobileWidth) {
      width: 100%;
    }
    
  }
  &__data {
    text-align: left;
    position: relative;
    font-size: 1.6rem;
    @media screen and(max-width: $mobileWidth) {
      font-size: 1rem;
    }
    &--header {
      text-transform: uppercase;
      color: lighten($textAccentColor, 20);
    }
    &--special,
    &--hit,
    &--new {
      padding-right: 3rem;
    }
    &--special::after,
    &--hit::after,
    &--new::after {
      text-transform: uppercase;
      position: absolute;
      top: 0;
      right: 2rem;
      background: $backgroundAccentColor;
      padding: 0.5rem 1rem;
      transform: rotate(18deg);
      font-size: 1.4rem;
      line-height: 1.2rem;
      border-radius: 0.9rem;
      @media screen and(max-width: $laptopWidth) {
        right: 0rem;
        padding: 0.5rem 0.6rem;
        font-size: 1rem;
        line-height: 1.1rem;
      }
      @media screen and(max-width: $mobileWidth) {
        right: 0rem;
        padding: 0.2rem 0.4rem;
        font-size: 0.7rem;
      }
      
    }
    &--special::after {
      content: 'акція';
      
    }
    &--hit::after {
      content: 'мега-хіт';
    }
    &--new::after {
      content: 'новинка';
    }
  }
  &__image-wrapper {
    width: 30%;
    @media screen and(max-width: $laptopWidth) {
      width: 50%;
    }
    @media screen and(max-width: $mobileWidth) {
      width: 60%;
    }
  }
  &__image {
    width: 100%;
    height: auto;
    object-fit: cover;
    object-position: center;
  }
}