.header {
  position: relative;
  background-color: $backgroundAccentColor;
  padding: 1rem; 
  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo {
    width: 7.5rem;
    height: 7.5rem;
    @media screen and (max-width: $laptopWidth) {
      width: 5.5rem;
      height: 5.5rem;
    }
    @media screen and (max-width: $mobileWidth) {
      width: 3.5rem;
      height: 3.5rem;
    }
  }
  &__menu {
    @media screen and (max-width: $laptopWidth) {
      flex-grow: 1;
      display: flex;
      justify-content: flex-end;
    }
  }
}