$laptopWidth: 992px;
$mobileWidth: 576px;

.title {
	font-family: "Lobster";
	font-weight: 400;
	color: $textMainColor;
	text-transform: uppercase;

	&--large {
		font-size: 5.4rem;
		line-height: 6.7rem;
	}
	&--medium {
		font-size: 3.4rem;
		line-height: 4.2rem;
	}
	&--small {
		font-size: 2.7rem;
		line-height: 3rem;
	}
	&--light {
		color: #ffffff;
	}
	&--accent {
		color: $textAccentColor;
	}
	&--404 {
		font-size: 40rem;
		line-height: 40.8rem;
		color: $textAccentColor; 
	}
	@media screen and (max-width: $mobileWidth) {
		&--large {
			font-size: 3.4rem;
			line-height: 4.2rem;
		}
		&--medium {
			font-size: 2.8rem;
			line-height: 3.5rem;
		}
		&--small {
			font-size: 2rem;
			line-height: 2.5rem;
		}
		&--404 {
			font-size: 17.4rem;
    		line-height: 18rem;
		}
	}
}