.about {
  padding: 6rem 0;
  background: url(../img/about_bg.jpg);
  background-color: $backgroundContrastColor;
  background-repeat: no-repeat; 
  background-size: cover;
  @media screen and (max-width: $mobileWidth) {
    padding: 3rem 0;
  }
  &__title {
    color: $textContrastColor;
    text-align: center;
    margin-bottom: 2rem;
  }
  &__wrapper {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    padding-top: 3rem;
    @media screen and (max-width: $mobileWidth) {
      flex-direction: column;
      padding-top: 0;
    }
  }  
  &__content,
  &__image-container {
    width: 50%;
  }
  &__content {
    padding: 4rem;
    & p {
      padding-bottom: 1.5rem;
    }
    @media screen and (max-width: $laptopWidth) {
      width: 60%;
      padding: 0rem;
      padding-left: 2rem;
    }
    @media screen and (max-width: $mobileWidth) {
      width: 90%;
      padding: 0;
    }
  }
  &__image-container {
    position: relative;
    @media screen and (max-width: $laptopWidth) {
      width: 40%;
    }
  }

  &__image {
    object-fit: cover;
    width: 100%;
    height: auto;
  }
}