.menu {
  &__content {
    @media screen and (max-width: $laptopWidth) {
      position: absolute;
      height: 0;
      width: 100%;
      top: 100%;
      left: 0;
      overflow: hidden;
      background-color: $backgroundAccentColor;
      transition: 0.2s all linear;
      
      &--open {
        min-height: 90vh;
      }
    }
  }
  &__list {
    display: flex;
    list-style: none;
    @media screen and (max-width: $laptopWidth) {
      flex-direction: column;
      padding-left: 0;
    }
  }
  &__item {
    margin-left: 0.2rem;
    @media screen and (max-width: $laptopWidth) {
      margin-left: 0;

    }
  }
  &__link {
    display: block;
    text-decoration: none;
    text-transform: uppercase;
    color: #ffffff;
    font-size: 1.4rem;
    padding: 2rem 1rem;
    transition: 0.3s all linear;
    @media screen and (max-width: $laptopWidth) {
      padding-left: 1.5rem;
    }
    &:hover {
      background-color: darken($backgroundAccentColor, 10);
      transform: scale(1.1);
      @media screen and (max-width: $laptopWidth) {
        transform: scale(1.02);
      }
    }

  }
  &__button {
    width: 3rem;
    height: 3rem;
    border: none;
    background: none;
    margin: 0;
    padding: 0;
    position: relative;
    cursor: pointer;
    display: none;
    @media screen and (max-width: $laptopWidth) {
      display: block;
    }
    
    &::before,
    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 0.5rem;
      background: white;
      border-radius: 2rem;
      position: absolute;
      left: 0;
      @media screen and (max-width: $mobileWidth) {
        height: 0.3rem;
      }
    }
    &::before {
      top: 0;
    }
    &::after {
      top: 100%;
      transform: translateY(-100%);
    }
  }
  &__burger {
    display: block;
    height: 0.5rem;
    background: white;
    border-radius: 2rem;
    @media screen and (max-width: $mobileWidth) {
      height: 0.3rem;
    }
  }
}