.promo {
  background-image: 
    url(../img/promo_bg.png),
    url(../img/promo_bg-bt.png),
    url(../img/promo-bg-lg.jpg);
  background-position: 
    0 0, 
    100% 100%,
    center center;
  background-repeat: 
    no-repeat,
    no-repeat, 
    no-repeat;
  background-size: 
    36rem, 
    36rem,
    cover;
  background-attachment: 
    scroll, 
    scroll, 
    fixed;
  padding: 15rem 2rem;
  @media screen and (max-width: $laptopWidth) {
    background-size: 26rem, 26rem, cover;
  }
  @media screen and (max-width: $mobileWidth) {
    background-size: 16rem, 16rem, cover;
  }
  // &__container {
  //   // width: 50%;
  //   // align-self: self-end;
  // }
  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title {
    margin-bottom: 2rem;
  }
  &__content {
    font-size: 3.4rem;
    line-height: 4rem;
    margin-bottom: 4rem;
  }
  // &__button {

  // }
}