.callback {
  background-image: url(../img/promo-bg-lg.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  padding: 6rem 0;
  @media screen and (max-width: $mobileWidth) {
    padding: 3rem 0;
  }
  &__title {
    text-align: center;
    margin-bottom: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    .icon {
      display: block;
      width: 12rem;
      height: 12rem;
      fill: currentColor;
      animation-name: up-dowm;
      animation-duration: 2s;
      animation-iteration-count: infinite;
      @media screen and (max-width: $laptopWidth) {
        width: 8rem;
        height: 8rem;
      }
      @media screen and (max-width: $mobileWidth) {
        width: 6rem;
        height: 6rem;
      }
    }
  }
  &__list {
    list-style: none;
    display: flex;
    margin: 5rem -2rem;
    padding: 0;
    justify-content: center;
    flex-wrap: wrap;
    @media screen and (max-width: $mobileWidth) {
      margin: 2rem -1rem;
    }
  }

  &__item {
    display: block;
    width: 9rem;
    height: 9rem;
    margin: 0 3rem;
    @media screen and (max-width: $mobileWidth) {
      margin: 0 2rem;
      width: 4rem;
      height: 4rem;
    }
  }
  &__link {
    color: $textMainColor;
    transition: 0.2s all linear;
    &:hover {
      color: $textAccentColor;
    }
  }
  &__icon {
    display: block;
    fill: currentColor;
    width: 100%;
    max-height: 100%;
  }
}

@keyframes up-dowm {
  50% {
    transform: translateY(30%);
  }
  100% {
    transform: translateY(0%);
  }
}
