.button {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2.4rem;
  color: $textMainColor;
  background-color: $backgroundAccentColor;
  text-transform: uppercase;
  text-decoration: none;
  padding: 2rem 2.4rem;
  border-radius: 2.4rem;
  cursor: pointer;
  transition: 0.2s all linear;
  &:hover {
    background-color: darken($backgroundAccentColor, 10);
  }
  &:active {
    box-shadow: 
      0.2rem 0.2rem 0 0.2rem darken($backgroundAccentColor, 20) inset,
      -0.2rem -0.2rem 0 0.2rem darken($backgroundAccentColor, 20) inset,
      -0.2rem 0.2rem 0 0.2rem darken($backgroundAccentColor, 20) inset,
      0.2rem -0.2rem 0 0.2rem darken($backgroundAccentColor, 20) inset;
  }
}