html {
  font-size: ($basicFontSize/$orientalWidth) * 100vw;
  font-family: $basicFontFamily, sans-serif;
  @media screen and (max-width: $laptopWidth) {
    font-size: ($basicFontSize/$orientalLaptopWidth) * 100vw;
  }
  @media screen and (max-width: $mobileWidth) {
    font-size: ($basicFontSize/$orientalMobileWidth) * 100vw;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p {
  padding: 0;
  margin: 0;
}

body {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: $backgroundMainColor;
  color: $textMainColor;
}

body > * {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.container {
  max-width: ($contentWidth/$orientalWidth) * 100vw;
  width: 100%;
  @media screen and (max-width: $laptopWidth) {
    max-width: ($contentLaptopWidth/$orientalLaptopWidth) * 100vw;
  }
  @media screen and (max-width: $mobileWidth) {
    max-width: ($contentMobileWidth/$orientalMobileWidth) * 100vw;
  }
}
