.product {
  padding: 6rem 0;
  @media screen and (max-width: $mobileWidth) {
    padding: 3rem 0;
  }
  &__title {
    text-align: center;
    margin-bottom: 2rem;
  }
  &__gallery {
    display: flex;
    flex-wrap: wrap;
    margin-left: -1.5rem;
    margin-right: -1.5rem;
    @media screen and (max-width: $mobileWidth) {
      margin-left: -1rem;
      margin-right: -1rem;
    }
  }
  &__item {
    display: block;
    position: relative;
    width: calc(25% - 3rem);
    max-height: 30rem;
    overflow: hidden;
    margin: 1.5rem;
    border-radius: 1rem 4rem 2rem 4rem;
    @media screen and (max-width: $laptopWidth) {
      width: calc(100% / 3 - 3rem);
      max-height: 20rem;
    }
    @media screen and (max-width: $mobileWidth) {
      width: calc(100% / 2 - 2rem);
      max-height: 17rem;
      margin: 1rem;
    }
    & img {
      max-width: 100%;
      height: auto;
      object-fit: cover;
      object-position: center;
    }
  }
}