.tabs {
  &__panel {
    display: flex;
    justify-content: flex-start;
  }
  &__tab {
    display: block;
    flex-grow: 1;
    padding: 1rem;
    background-color: lighten($backgroundMainColor, 20);
    border-radius: 1rem 1rem 0 0;
    text-transform: uppercase;
    text-decoration: none;
    
    &--active {
      background-color: $backgroundMainColor;
    }
    @media screen and(max-width: $mobileWidth) {
      font-size: 1.2rem;
    }
  }
  &__screen {
    background-image: url(../img/promo-bg-lg.jpg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 0 0 1rem 1rem;
    //Temp
    min-height: 33rem;

  }
  &__content {
    display: none;
    padding: 4rem 2rem;
    &--active {
      display: block;
    }
  }
  &__row {
    margin-bottom: 3rem;
    padding: 2rem;
    @media screen and(max-width: $mobileWidth) {
      margin-bottom: 1rem;
      padding: 1rem;
    }
  }
}