.price {
  padding: 6rem 0;
  background-image: url(../img/about_bg.jpg);
  background-color: $backgroundContrastColor;
  background-size: cover;
  @media screen and (max-width: $mobileWidth) {
    padding: 3rem 0;
  }
  &__title {
    text-align: center;
    margin-bottom: 2rem;
    color: $textContrastColor;
  }
  &__wrapper {
    width: 100%;
  }
}