@import "../_vars";

$laptopWidth: 992px;
$mobileWidth: 576px;

.content {
	font-family: "Montserrat";
	font-weight: 400;
	font-size: 1.8rem;
	line-height: 2.7rem;
	color: $textMainColor;

	& sup {
		font-size: 1.5rem;
		line-height: 1.5rem;
	}

	&--large {
		font-size: 2.4rem;
		line-height: 3rem;
	}

	&--medium {
		font-size: 2rem;
		line-height: 3.2rem;
	}

	&--small {
		font-size: 1.8rem;
		line-height: 2.7rem;
	}

	&--dark {
		color: #000000;
	}

	&--accent {
		text-transform: uppercase;
		color: #1136C7;
	}

	&--light {
		color: #ffffff;
	}
	@media screen and (max-width: $mobileWidth) {
		font-size: 1.4rem;
		line-height: 2.1rem;
		&--large {
			font-size: 1.8rem;
			line-height: 2rem;
		}
	}
}